<template>
  <v-row class="justify-center">
    <v-col cols="12" sm="6" md="4" class="dti-wraper">
      <v-spacer></v-spacer>
      <datetime v-model="date" :max-datetime="currentDateTime" type="datetime" input-id="startDate">
        <label for="startDate" slot="before">
          <v-icon>mdi-calendar</v-icon>
          <span class="ml-2"> </span>
        </label>
        <template slot="button-cancel"></template>
        <template slot="button-confirm"></template>
      </datetime>
    </v-col>
    <v-col cols="12" sm="6" md="4" class="dti-wraper">
      <datetime
        v-model="date2"
        :max-datetime="currentDateTime"
        :min-datetime="date"
        type="datetime"
        input-id="endDate"
      >
        <label for="endDate" slot="before">
          <v-icon>mdi-calendar</v-icon>
          <span class="ml-2"> </span>
        </label>
        <template slot="button-cancel"></template>
        <template slot="button-confirm"></template>
      </datetime>
    </v-col>
    <v-col cols="12" sm="6" md="4">
      <v-spacer></v-spacer>
      <v-btn @click="getStatistics" class="ma-2 float-right" tile color="#34495e" dark></v-btn>
    </v-col>
  </v-row>
</template>
<script>
import { Datetime } from "vue-datetime";

export default {
  components: {
    Datetime
  },
  created() {
    this.date = moment()
      .subtract(1, 'days')
      .hour(0)
      .minute(0)
      .second(0)
      .toISOString();
    this.date2 = moment()
      .hour(0)
      .minute(0)
      .second(0)
      .toISOString();
  },
  data: () => ({
    menu: false,
    menu2: false
  }),
  computed: {
    date: {
      get: function() {
        return this.$store.state.messages_tab.date;
      },
      set: function(newValue) {
        this.$store.commit("messages_tab/saveDate", newValue);
      }
    },
    date2: {
      get: function() {
        return this.$store.state.messages_tab.date2;
      },
      set: function(newValue) {
        this.$store.commit("messages_tab/saveDate2", newValue);
      }
    },
    currentDateTime() {
      return moment().toISOString();
    }
  },
  methods: {
    getStatistics() {
      this.$store.dispatch("messages_tab/getStatistics");
    }
  }
};
</script>
<style lang="scss" scoped>
.dti-wraper {
  max-width: 294px;
}
</style>