<template>
  <v-list>
    <!-- <v-subheader>{{subheader}}</v-subheader> -->
    <template v-if="subheader">
      <v-list-item @click="clickedAll">
        <v-list-item-icon>
          <v-icon color="#000" v-if="subheader.isActive">mdi-check-circle-outline</v-icon>
          <v-icon v-else>mdi-circle-outline</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title class="font-weight-bold" v-text="subheader.name"></v-list-item-title>
        </v-list-item-content>
      </v-list-item>
      <v-divider></v-divider>
    </template>
    <v-list-item
      v-for="(item, i) in items"
      :key="i"
      @click="clickedOne({itemName: item.name,collectionName: collectionName,index: i})"
      active
    >
      <v-list-item-icon>
        <v-icon :color="item.color" v-if="item.isActive">mdi-check-circle-outline</v-icon>
        <v-icon v-else>mdi-circle-outline</v-icon>
      </v-list-item-icon>
      <v-list-item-content>
        <v-list-item-title v-text="item.displayName"></v-list-item-title>
        <v-list-item-subtitle v-if="item.subtitle" v-text="item.subtitle"></v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>
  </v-list>
</template>
<script>
export default {
  mounted() {},
  data: () => ({
    model: 0
  }),
  props: ["items", "subheader", "actions", "collectionName"],
  methods: {
    clickedOne(data) {
      this.$store.dispatch(this.actions.clickedOne, data);
    },
    clickedAll() {
      this.$store.dispatch(this.actions.clickedAll);
    }
  }
};
</script>