<template>
  <v-card>
    <v-list>
      <v-list-item-group v-model="model">
        <v-subheader>{{subheader}}</v-subheader>
        <v-list-item v-for="(item, i) in items" :key="i">
          <v-list-item-icon>
            <v-icon v-if="item.isActive">mdi-check-circle-outline</v-icon>
            <v-icon v-else>mdi-circle-outline</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title class="text-uppercase" v-text="item.name"></v-list-item-title>
             <v-list-item-subtitle v-text="item.time"></v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-list>
  </v-card>
</template>
<script>
export default {
  data: () => ({
    model: 0,
  }),
  props:['items','subheader'],
  watch:{
    model:function(index){
      this.items.forEach(el=>{el.isActive = false})
      this.items[index].isActive = true
      this.$store.dispatch('messages_tab/choosePreset',this.items[index])
    }
  }
};
</script>