<template>
  <div>
    <v-select v-model="model" :items="items" label=""></v-select>
  </div>
</template>
<script>
export default {
  props: ["data"],
  data: () => ({
    model: null
  }),
  computed: {
    items() {
       return this.data.map(function(item, index, array) {
        return item.displayName
      });
    }
  },
  watch:{
    model(){
      this.$store.dispatch("messages_tab/togglePreset", this.model);
    }
  }
};
</script>