<template>
  <v-row>
    <v-col cols="12" sm="6" md="4">
      <v-spacer></v-spacer>
      <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="date"
        transition="scale-transition"
        offset-y
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="date"
            label=" "
            prepend-icon="mdi-calendar"
            readonly
            v-on="on"
          ></v-text-field>
        </template>
        <v-date-picker :rules="false" first-day-of-week="1" locale="ru" v-model="date" no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="menu = false"></v-btn>
          <v-btn text color="primary" @click="$refs.menu.save(date)">OK</v-btn>
        </v-date-picker>
      </v-menu>
    </v-col>
    <v-spacer></v-spacer>
    <v-col cols="12" sm="6" md="4">
      <v-menu
        ref="menu2"
        v-model="menu2"
        :close-on-content-click="false"
        :return-value.sync="date2"
        transition="scale-transition"
        offset-y
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="date2"
            label=" "
            prepend-icon="mdi-calendar"
            readonly
            v-on="on"
          ></v-text-field>
        </template>
        <v-date-picker first-day-of-week="1" locale="ru" v-model="date2" no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="menu2 = false"></v-btn>
          <v-btn text color="primary" @click="$refs.menu2.save(date2)">OK</v-btn>
        </v-date-picker>
      </v-menu>
    </v-col>
    <v-col cols="12" sm="6" md="4">
      <v-spacer></v-spacer>
      <v-btn @click="getStatistics" class="ma-2 float-right" tile color="indigo" dark></v-btn>
    </v-col>
  </v-row>
</template>
<script>
export default {
  data: () => ({
    menu: false,
    menu2: false
  }),
  computed: {
    date: {
      get: function() {
        return this.$store.state.messages_tab.date;
      },
      set: function(newValue) {
        this.$store.commit("messages_tab/saveDate", newValue);
      }
    },
    date2: {
      get: function() {
        return this.$store.state.messages_tab.date2;
      },
      set: function(newValue) {
        this.$store.commit("messages_tab/saveDate2", newValue);
      }
    }
  },
  methods: {
    getStatistics() {
      this.$store.dispatch("messages_tab/getStatistics");
    }
  }
};
</script>