<script>
export default {
  props: ["src"],
  data: function() {
    return {
      playing: false,
      audio_now: "00:00:00",
      audio_left: "00:00:00",
      show: false,
      moving: false,
      speed_value: 1,
      volume: 1
    };
  },
  updated() {
    if (this.show) {
      let volume = this.$el.querySelectorAll(".volume__bar")[0];
      this.change_position(this.audio.volume * 100, volume);
      this.volume = this.audio.volume;
    }
  },
  computed: {
    audio: function() {
      return this.$el.getElementsByTagName("audio")[0];
    },
    progress_bar: function() {
      return this.$el.querySelectorAll(".progress")[0];
    },
    toggle_text() {
      return this.show ? "" : "";
    }
  },
  methods: {
    clicked() {
      alert("clicked");
    },
    create_time_string(value) {
      let hours = Math.floor(value / 3600);
      let minutes = Math.floor((value - hours * 3600) / 60);
      let seconds = Math.floor(value - minutes * 60 - hours * 3600);
      if (seconds < 10) seconds = "0" + seconds;
      if (minutes < 10) minutes = "0" + minutes;
      if (hours < 10) hours = "0" + hours;
      return hours + ":" + minutes + ":" + seconds;
    },
    play_or_pause() {
      if (this.audio.paused) {
        this.audio.play();
        this.playing = true;
      } else {
        this.audio.pause();
        this.playing = false;
      }
    },
    when_ended() {
      this.playing = false;
    },
    upadate_progress() {
      this.audio_now = this.create_time_string(this.audio.currentTime);
      this.audio_left = this.create_time_string(
        this.audio.duration - this.audio.currentTime
      );
      let value = (this.audio.currentTime / this.audio.duration) * 100;
      this.progress_bar.style.background = `linear-gradient(to right,#55A5FF 0%,#55A5FF ${value}%, #e7e7e7 ${value}%, #e7e7e7 100%)`;
    },
    progress_click(event) {
      this.moving = true;
      this.change_progress(event);
      this.moving = false;
    },
    change_progress(event) {
      let value = this.calculate_position(event);
      if (value != undefined)
        this.audio.currentTime = Math.floor(
          (value / 100) * this.audio.duration
        );
    },
    volume_click(event) {
      this.moving = true;
      this.change_volume(event);
      this.moving = false;
    },
    change_volume(event) {
      let value = this.calculate_position(event);
      if (value != undefined) {
        this.audio.volume = value / 100;
        this.volume = value / 100;
      }
    },
    change_speed(event) {
      this.speed_value = event.target.value;
      this.audio.playbackRate = +event.target.value;
    },
    calculate_position(event) {
      let bar = event.target;
      if (this.moving) {
        const width = Math.floor(bar.getBoundingClientRect().width);
        const position = Math.floor(bar.getBoundingClientRect().left);
        let new_position = event.clientX - position;
        if (event.clientX < position) new_position = 0;
        if (new_position >= width) new_position = width;
        let result = (new_position / width) * 100;
        this.change_position(result, bar);
        return result;
      }
    },
    change_position(value, bar) {
      bar.style.background = `linear-gradient(to right,#55A5FF 0%,#55A5FF ${value}%, #e7e7e7 ${value}%, #e7e7e7 100%)`;
    },
    moving_activation() {
      this.moving = true;
    },
    moving_disactivation() {
      this.moving = false;
    },
    toggle_player() {
      this.show = !this.show;
    }
  }
};
</script>