<template>
  <div>
    <v-card flat tile>
        <v-row>
          <v-col cols="12">
            <v-card style="border: 0;" class="pa-2" outlined tile>
              <date-time-interval-picker style="margin-bottom:30px" />
              <e2 />
            </v-card>
          </v-col>
          <v-col cols="12">
            <v-card class="pa-2" outlined tile>
              <v-divider />
              <multiple-list
                class="mb-2"
                :items="labels.items"
                :subheader="labels.channel.subheader"
                :actions="labels.actions"
                :collectionName="labels.collectionName"
                :showType="types.channel"
              >
                <span slot="title"></span>
              </multiple-list>
            </v-card>
          </v-col>
          <v-col cols="12">
            <date-time-interval-picker style="margin-top:30px" />
          </v-col>
        </v-row>
    </v-card>
    <new-preset-dialog />
  </div>
</template>
<script>
import TestChart from "../components/TestChart.vue";
import TestChartLine from "../components/TestChartLine.vue";
import ListItem from "../components/ListItem.component.vue";
import MultipleList from "../components/MultipleList.component.vue";
import MultipleListTest from "../components/MultipleListTest.component.vue";
import player from "../components/player.vue";
import DatePicker from "../components/DatePicker.component.vue";
import SingleSwitch from "../components/SingleSwitch.component.vue";
import DynamicTable from "../components/DynamicTable.component.vue";
import StatisticTable from "../components/StatisticTable.component.vue";
import ExpansionPanel from "../components/ExpansionPanel.component.vue";
import Test from "../components/Select.component.vue";
import NewPresetDialog from "../components/NewPresetDialog.component.vue";

import DateTimeIntervalPicker from "@/components/DateTimeIntervalPicker.vue";

import Echart from "@/components/Echart.vue";

import LineChart from "../components/LineChart.vue";
import rdata from "../store/random_data3.json";
import e2 from "@/components/e2.vue";

async function get_programs() {
  return axios.get(
    "http://dev533.iactive.pro/scr/efir.php?userName=79671035533&dateFrom=2020-02-02&dateTo=2020-02-02"
  );
}
export default {
  async created() {
    let programs = {
      collectionName: "programs",
      subheader: {
        isActive: true,
        name: ""
      },
      actions: {
        clickedOne: "messages_tab/toggleProgram",
        clickedAll: "messages_tab/toggleAllPrograms"
      },
      items: []
    };
    function intToTime(value) {
      let h = Math.floor(value / 60);
      let m = Math.floor(value - h * 60);
      m = m < 10 ? "0" + m : m;
      return h + ":" + m;
    }
    let response = await get_programs();
    let data = response.data;
    data.forEach(element => {
      let subtitle = intToTime(element[0]);
      programs.items.push({
        isActive: true,
        displayName: element[2],
        subtitle: subtitle,
        start: element[0],
        end: element[1]
      });
    });

    this.$store.commit("messages_tab/updatePrograms", programs);
  },
  components: {
    TestChart,
    TestChartLine,
    ListItem,
    MultipleList,
    MultipleListTest,
    player,
    DatePicker,
    LineChart,
    SingleSwitch,
    DynamicTable,
    StatisticTable,
    ExpansionPanel,
    Test,
    NewPresetDialog,
    Echart,
    DateTimeIntervalPicker,
    e2
  },
  data() {
    return {
      tab: null,
      data: rdata,
      elements: ["", "", ""],
      types: {
        channel: "channel",
        author: "author"
      }
    };
  },
  computed: {
    labels() {
      return this.$store.state.messages_tab.labels;
    },
    programs() {
      return this.$store.state.messages_tab.programs;
    },
    authors() {
      return this.$store.state.messages_tab.authors;
    },
    presets() {
      return this.$store.state.messages_tab.presets;
    }
  }
};
</script>
<style scoped>
.headline-for-tables {
  margin-bottom: 30px;
  font-size: 40px;
  color: #616161;
  text-align: center;
}
</style>