<template>
  <v-row justify="center">
    <v-dialog v-model="dialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline">  </span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-text-field label="*" v-model="name" required></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-autocomplete
                  :items="labels"
                  label="*"
                  multiple
                  v-model="selectedLabels"
                  required
                ></v-autocomplete>
              </v-col>
            </v-row>
          </v-container>
          <small>*   </small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="$store.commit('messages_tab/updateNewPresetDialog', false);"
          ></v-btn>
          <v-btn color="blue darken-1" text @click="savePreset"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>
<script>
export default {
  data: () => ({
    name: "",
    selectedLabels: []
  }),
  computed: {
    labels() {
      return this.$store.state.messages_tab.labels.items.map(function(
        item,
        index,
        array
      ) {
        return item.displayName;
      });
    },
    dialog() {
      return this.$store.state.messages_tab.newPresetDialog;
    }
  },
  methods: {
    savePreset() {
      this.$store.commit("messages_tab/updateNewPresetDialog", false);
      this.$store.dispatch("messages_tab/savePreset", {
        name: this.name,
        labels: this.selectedLabels
      });
    }
  }
};
</script>