<template>
  <v-card>
    <v-list>
      <!-- <v-subheader>{{subheader}}</v-subheader> -->
      <v-list-item @click="clickedAll">
        <v-list-item-icon>
          <v-icon color="#000" v-if="subheader.isActive">mdi-check-circle-outline</v-icon>
          <v-icon v-else>mdi-circle-outline</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title class="font-weight-bold">
            <slot name="title">  </slot>
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>
      <v-divider></v-divider>
      <div class="ml-list">
        <template v-for="(item, i) in items">
          <v-list-item
            v-if="item.type == showType"
            :key="i"
            @click="clickedOne({itemName: item.name,collectionName: collectionName,index: i,type: showType})"
            active
            class="ml-list__item"
          >
            <v-list-item-icon>
              <v-icon :color="item.color" v-if="item.isActive">mdi-check-circle-outline</v-icon>
              <v-icon v-else>mdi-circle-outline</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title v-text="item.displayName"></v-list-item-title>
              <v-list-item-subtitle v-if="item.subtitle" v-text="item.subtitle"></v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
        </template>
      </div>
    </v-list>
  </v-card>
</template>
<script>
export default {
  mounted() {},
  data: () => ({
    model: 0
  }),
  props: ["items", "subheader", "actions", "collectionName", "showType"],
  methods: {
    clickedOne(data) {
      this.$store.dispatch(this.actions.clickedOne, data);
    },
    clickedAll() {
      this.$store.dispatch(this.actions.clickedAll, this.showType);
    }
  }
};
</script>
<style lang="scss" scoped>
.ml-list {
  display: flex;
  flex-wrap: wrap;
}
.ml-list__item{
  flex: 0 1 25%;
  max-width: 285px;
}
</style>