    ) {
      const userName = "+79619002008";
      const socketToken = "44cxixhsd2g4xqq7dmzgd6:localhost";
      const token = "000000000000000000";

      return new Promise(function(resolve, reject) {
        const formData = new FormData();
        formData.append("userName", userName);
        formData.append("socketToken", socketToken);
        formData.append("token", token);
        for (let param in data) formData.append(param, data[param]);
        var xhr = new XMLHttpRequest();
        xhr.open(type, url, sync);
        xhr.onload = function() {
          if (this.status >= 200 && this.status < 300) {
            resolve(xhr.response);
          } else {
            reject({
              status: this.status,
              statusText: xhr.statusText
            });
          }
        };
        xhr.onerror = function() {
          reject({
            status: this.status,
            statusText: xhr.statusText
          });
        };
        xhr.send(formData);
      });
