<template>
  <div>
    <apexcharts
      ref="main_chart_ref"
      type="line"
      :options="chartOptions"
      :series="series"
      max-width="1000"
      height="400"
    ></apexcharts>
    <player style="padding-left: 45px;" src="/record1.mp3" />
    <apexcharts
      ref="map_chart_ref"
      :options="chartOptionsLine"
      :series="series"
      max-width="1000"
      height="200"
      id="map_chart"
    ></apexcharts>
  </div>
</template>
<script>
import VueApexCharts from "vue-apexcharts";
import rdata from "../store/random_data4.json";
import annotations from "../store/annotation.json";
import player from "../components/player.vue";
let ax = require("axios");
async function get_data() {
  return axios.get(
    "http://dev533.iactive.pro/scr/statisctic.php?userName=79671035533&dateFrom=2020-02-02&dateTo=2020-02-02"
  );
}
function get_programs() {
  return ax.get(
    "http://dev533.iactive.pro/scr/efir.php?userName=79671035533&dateFrom=2020-02-02&dateTo=2020-02-02"
  );
}

export default {
  async created() {
    try {
      let response = await get_data();
      this.$store.commit("messages_tab/updateAllStatistics", response.data);
      this.updateChart(response.data);
      //this.$store.dispatch("messages_tab/updateChart", response.data);
    } catch (ex) {
      console.log(ex);
    }
  },
  mounted() {
    this.$store.commit(
      "messages_tab/updateMainChart",
      this.$refs.main_chart_ref
    );
    this.$store.commit("messages_tab/updateMapChart", this.$refs.map_chart_ref);
  },
  components: {
    apexcharts: VueApexCharts,
    player
  },
  data: function() {
    return {
      chartOptions: {
        chart: {
          id: "main_chart",
          zoom: {
            enabled: false
          },
          stacked: false,
          sparkline: {
            enabled: false
          },
          toolbar: {
            show: false
          },
          animations: {
            enabled: false
          }
        },
        markers: {
          size: 0
        },
        annotations: annotations,
        xaxis: {
          type: "numeric",
          tickAmount: 9,
          labels: {
            hideOverlappingLabels: false,
            formatter: function(value, timestamp, index) {
              let h = Math.floor(value / 60);
              let m = Math.floor(value - h * 60);
              m = m < 10 ? "0" + m : m;
              if (value >= 24) {
                let d = Math.floor(h / 24);
                h -= 24 * d;
              }
              return h + ":" + m;
            }
          }
        },
        yaxis: {
          decimalsInFloat: 0,
          tickAmount: 2
          // decimalsInFloat: 1,
          // min: 0,
          // max: 6,
          // // labels: {
          // //   formatter: value => {
          // //     return Math.floor(value);
          // //   }
          // // }
        },
        legend: {
          show: false
        },
        stroke: {
          show: true,
          lineCap: "butt",
          colors: undefined,
          width: 2,
          dashArray: 0
        },
        dataLabels: {
          enabled: false
        },
        colors: [
          "#000",
          "#2E93fA",
          "#66DA26",
          "#546E7A",
          "#E91E63",
          "#FF9800",
          "#000051",
          "#e91e63",
          "#f4511e",
          "#4b2c20",
          "#9fa8da",
          "#c6ff00",
          "#ab000d"
        ]
      },
      chartOptionsLine: {
        chart: {
          type: "area",
          brush: {
            target: "main_chart",
            enabled: true
          },
          // stacked: true,
          selection: {
            enabled: true,
            xaxis: {
              min: 0,
              max: 45
            }
          }
        },
        // fill: {
        //   colors: ["#2E93fA", "#66DA26", "#546E7A", "#E91E63", "#FF9800"]
        // },
        stroke: {
          show: false
        },
        legend: {
          show: false
        },
        xaxis: {
          type: "numeric",
          tooltip: {
            enabled: false
          },
          tickAmount: 9,
          labels: {
            hideOverlappingLabels: false,
            formatter: function(value, timestamp, index) {
              let h = Math.floor(value / 60);
              let m = Math.floor(value - h * 60);
              m = m < 10 ? "0" + m : m;
              if (value >= 24) {
                let d = Math.floor(h / 24);
                h -= 24 * d;
              }
              return h + ":" + m;
            }
          }
        },
        colors: [
          "#000",
          "#2E93fA",
          "#66DA26",
          "#546E7A",
          "#E91E63",
          "#FF9800",
          "#000051",
          "#e91e63",
          "#f4511e",
          "#4b2c20",
          "#9fa8da",
          "#c6ff00",
          "#ab000d"
        ]
      },
      series: []
    };
  },
  methods: {
    doSome() {
      this.$refs.map_chart_ref.addXaxisAnnotation({
        x: 40,
        label: {
          text: "Lorem Ipsum"
        }
      });
    },
    updateChart(data) {
      this.$store.commit("messages_tab/updateServerData", data);
      let arrKeys = Object.keys(data);
      arrKeys = arrKeys.slice(0, arrKeys.length - 1);
      let allSeries = [];

      arrKeys.forEach(element => {
        allSeries = allSeries.concat(data[element]);
      });

      let series = [
        {
          name: " ",
          data: allSeries.map(elem => [
            elem[0],
            elem.slice(1).reduce((a, b) => Math.round(a + b))
          ])
        }
      ];
      data.channels.forEach((item, index, array) => {
        series.push({
          name: item,
          data: allSeries.map(elem => [
            elem[0],
            Math.round(Number(elem[index + 1]))
          ])
        });
      });
      this.$store.dispatch("messages_tab/createLabels", series);
      this.$refs.main_chart_ref.updateSeries(series);
      this.$refs.map_chart_ref.updateSeries(series);
    }
  }
};
</script>