<template>
  <div id="chart">
    <apexchart height="400" :options="chartOptions" :series="series"></apexchart>
  </div>
</template>
<script>
import VueApexCharts from "vue-apexcharts";
import rdata from "../store/random_data3.json";

let series = [
  {
    name: "All",
    data: rdata.map(elem => [
      elem[0],
      elem.slice(1).reduce((a, b) => Number(a) + Number(b))
    ])
  },
  {
    name: "Facebook",
    data: rdata.map(elem => {
      return [elem[0], Number(elem[1])];
    })
  },
  {
    name: "Telegram",
    data: rdata.map(elem => {
      return [elem[0], Number(elem[2])];
    })
  },
  {
    name: "Viber",
    data: rdata.map(elem => {
      return [elem[0], Number(elem[3])];
    })
  },
  {
    name: "What`s App",
    data: rdata.map(elem => {
      return [elem[0], Number(elem[4])];
    })
  },
  {
    name: "Vkontakte",
    data: rdata.map(elem => {
      return [elem[0], Number(elem[5])];
    })
  }
];
let seriesAll = [...series];
export default {
  components: {
    apexchart: VueApexCharts
  },
  data: function() {
    return {
      series: series,
      chartOptions: {
        chart: {
          height: 350,
          type: "line",
          zoom: {
            enabled: false
          },
          toolbar: {
            show: false
          }
        },
        colors: ["#000", "#3b5998", "#0088cc", "#59267c", "#075e54", "#45668e"],
        xaxis: {
          type: "numeric",
          tooltip: {
            enabled: false
          },
          tickAmount: 9,
          labels: {
            hideOverlappingLabels: false,
            formatter: function(value, timestamp, index) {
              let h = Math.floor(value / 60);
              let m = Math.floor(value - h * 60);
              m = m < 10 ? "0" + m : m;
              return h + ":" + m;
            }
          }
        },
        dataLabels: {
          enabled: false
        },
        stroke: {
          curve: "straight",
          width: 2
        },
        title: {
          text: " ",
          align: "left"
        },
        grid: {
          row: {
            colors: ["#f3f3f3", "transparent"], // takes an array which will be repeated on columns
            opacity: 0.5
          }
        }
        // xaxis: {

        // }
      }
    };
  }
};
</script>